<template>
  <b-navbar toggleable="md" type="light" variant="light" id="top-bar">
    <b-navbar-brand to="/">
      <img src="/static/images/logo-horizontal-h53.png" alt="">
    </b-navbar-brand>

    <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

    <b-collapse is-nav id="nav_collapse">

      <b-navbar-nav class="ml-auto">
        <b-nav-item-dropdown text="Lang" right>
          <button class="dropdown-item" type="button" @click="$i18n.locale = 'en'">English</button>
          <button class="dropdown-item" type="button" @click="$i18n.locale = 'bg'">Български</button>
        </b-nav-item-dropdown>
      </b-navbar-nav>

    </b-collapse>

  </b-navbar>
</template>


<script>
export default {
  data() {
    return {
      isDev: process.env.APP_DEBUG,
    }
  },
}
</script>



