import Vue from 'vue'

// Import always before all components
import './styles/main-begin.scss'

import Vuelidate from 'vuelidate'
import BootstrapVue from 'bootstrap-vue'
import VueCharts from 'vue-charts'
import axios from 'axios'

import App from './App'
import router from './router'
import { store } from './store/store'
import Loading from '../components/Loading'

import VueI18n from 'vue-i18n'
import translations from './i18n'

// Import always after all components
import './styles/main-end.scss'

Vue.config.productionTip = !process.env.APP_DEBUG

store.dispatch('loadUsersCash')

axios.interceptors.request.use(function(config) {
  config.headers = config.headers || {}
  let token = store.state.users.csrfToken
  if (token) {
    config.headers['X-CSRF-Token'] = token
  }
  config.withCredentials = true
  return config
})

axios.interceptors.response.use(function(response) {
  return response
}, function(errorObj) {
  if (errorObj.response.status === 401) {
    // The user session is not valid or expired
    store.dispatch('logout')
  }
  return Promise.reject(errorObj)
})

Vue.use(VueI18n)
Vue.use(BootstrapVue)
Vue.use(Vuelidate)
Vue.use(VueCharts)
Vue.component('loading', Loading)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  i18n: new VueI18n({
    locale: 'bg',
    messages: translations,
  }),
  render: h => h(App),
})



