<template>
  <form @submit.prevent="submitHandler">

    <div class="alert alert-danger text-center" v-if="errorOnChange">
      {{ errorOnChange }}
    </div>

    <div class="form-group row">
      <label class="col-sm-2 col-form-label col-form-label-sm">Old password</label>
      <div class="col-sm-10">
        <input type="password"
          class="form-control form-control-sm"
          :class="[{ 'is-invalid': !$v.formData.oldPassword.required && $v.formData.oldPassword.$dirty }]"
          placeholder="Old Password"
          @blur="$v.formData.oldPassword.$touch()"
          v-model="formData.oldPassword">
        <div class="invalid-feedback">
          Please, enter old password.
        </div>
      </div>
    </div>

    <div class="form-group row">
      <label class="col-sm-2 col-form-label col-form-label-sm">New Password</label>
      <div class="col-sm-10">
        <input type="password"
          class="form-control form-control-sm"
          :class="[{ 'is-invalid': !$v.formData.newPassword.required && $v.formData.newPassword.$dirty }]"
          placeholder="New Password"
          @blur="$v.formData.newPassword.$touch()"
          v-model="formData.newPassword">
        <div class="invalid-feedback">
          Please, enter the new password
        </div>
      </div>
    </div>

    <div class="form-group row">
      <label class="col-sm-2 col-form-label col-form-label-sm">Repeat New Password</label>
      <div class="col-sm-10">
        <input type="password"
          class="form-control form-control-sm"
          :class="[{ 'is-invalid': (!$v.formData.repNewPassword.required && $v.formData.repNewPassword.$dirty) || !$v.formData.repNewPassword.sameAsNewPass }]"
          placeholder="Repeat New Password"
          @blur="$v.formData.repNewPassword.$touch()"
          v-model="formData.repNewPassword">
        <div class="invalid-feedback">
          {{ $v.formData.repNewPassword.sameAsNewPass ? 'Repeated password is not the same' : 'Please, repeat the new password' }}
        </div>
      </div>
    </div>

    <button type="submit" class="btn btn-lg btn-block btn-success" :disabled="$v.formData.$invalid">Change Password</button>

    <loading :loading="loading"></loading>

  </form>
</template>


<script>
import { required, sameAs } from 'vuelidate/lib/validators'
import usersService from '../../services/userService'

export default {
  props: {
    csrfToken: {},
  },
  validations: {
    formData: {
      oldPassword: {
        required,
      },
      newPassword: {
        required,
      },
      repNewPassword: {
        required,
        sameAsNewPass: sameAs('newPassword'),
      },
    },
  },
  data() {
    return {
      formData: {
        oldPassword: null,
        newPassword: null,
        repNewPassword: null,
      },
      loading: false,
      errorOnChange: null,
    }
  },
  methods: {
    submitHandler() {
      if (!this.$v.formData.$invalid) {
        this.loading = true
        usersService.changePass(this.formData.oldPassword, this.formData.newPassword)
          .then(res => {
            this.$emit('changeSuccess')
          })
          .catch(() => {
            // TODO: provide a better error message
            this.errorOnChange = 'OOPSS! Something goes wrong. Try again.'
          })
          .finally(() => {
            this.loading = false
          })
      }
    },
  },
}
</script>



