<template>
  <form @submit.prevent="submitHandler">

    <div class="alert alert-danger text-center" v-if="errorOnAction">
      {{ errorOnAction }}
    </div>

    <div class="form-group row">
      <label class="col-sm-2 col-form-label col-form-label-sm">Password</label>
      <div class="col-sm-10">
        <input type="password"
          class="form-control form-control-sm"
          :class="[{ 'is-invalid': !$v.formData.password.required && $v.formData.password.$dirty }]"
          placeholder="Password"
          @blur="$v.formData.password.$touch()"
          v-model="formData.password">
        <div class="invalid-feedback">
          Please, enter the password
        </div>
      </div>
    </div>

    <div class="form-group row">
      <label class="col-sm-2 col-form-label col-form-label-sm">Repeat Password</label>
      <div class="col-sm-10">
        <input type="password"
          class="form-control form-control-sm"
          :class="[{ 'is-invalid': (!$v.formData.repPassword.required && $v.formData.repPassword.$dirty) || !$v.formData.repPassword.sameAsPass }]"
          placeholder="Repeat Password"
          @blur="$v.formData.repPassword.$touch()"
          v-model="formData.repPassword">
        <div class="invalid-feedback">
          {{ $v.formData.repPassword.sameAsPass ? 'Repeated password is not the same' : 'Please, repeat the password' }}
        </div>
      </div>
    </div>

    <button type="submit" class="btn btn-lg btn-block btn-success" :disabled="$v.formData.$invalid">Set Password</button>

    <loading :loading="loading"></loading>

  </form>
</template>


<script>
import { required, sameAs } from 'vuelidate/lib/validators'
import userService from '../../services/userService'

export default {
  props: ['id', 'token'],
  validations: {
    formData: {
      password: {
        required,
      },
      repPassword: {
        required,
        sameAsPass: sameAs('password'),
      },
    },
  },
  data() {
    return {
      formData: {
        password: null,
        repPassword: null,
      },
      loading: false,
      errorOnAction: null,
    }
  },
  methods: {
    submitHandler() {
      if (!this.$v.formData.$invalid) {
        this.loading = true
        userService.setNewPass(this.token, this.id, this.formData.password)
          .then(res => {
            this.$emit('actionSuccess')
          })
          .catch(errObj => {
            this.errorOnAction = errObj.response.error
          })
          .finally(() => {
            this.loading = false
          })
      }
    },
  },
}
</script>



